:root {
    --background: #20b2aa;
    --border-radius: 20px;
}

.App {
    text-align: center;
    height: 100vh;

}

body {
    background-color: var(--background);
}

.header-search {
    /*background-color: rgba(32, 178, 170, 0.29);*/
    /*box-shadow: 0 0 20px 30px rgba(32, 178, 170, 0.29);*/
}

.main-search {
    box-shadow: 0 0 20px 20px rgba(32, 178, 170, 0.32);
}



._img-container {
    background: url(logo-og.png) 50% 50% no-repeat; /* 50% 50% centers image in div */
    background-size: cover;
    border-radius: var(--border-radius);
}

.small-card-img {
    background: url(logo-og.png) 50% 50% no-repeat; /* 50% 50% centers image in div */
    width: 30px;
    height: 30px;
    background-size: cover;
    border-radius: var(--border-radius);
}

/*.card-img {*/
/*  object-fit: cover;*/
/*  width:230px;*/
/*  height:230px;*/
/*}*/

.card{
    border-radius: var(--border-radius);
}

._bigcard {
    background-color: var(--light);
    border-radius: var(--border-radius);

    /*background-color: #a1dea1;*/
    /*box-shadow: 0 0 8px 4px gray;*/
}

.card-icon {
    /*color: grey;*/
    margin-right: 1.3rem;
    font-size: 1.4rem;
}

.card-icon:hover {
    color: var(--primary);
}

.search-icon {
    font-size: 1.2rem;
}

.left-panel-icon:hover {
    color: var(--primary);
}

.mainPageLeftIcon:hover {
    cursor: default;
}

._showMore {
    border: 2px solid black;
    border-radius: 1.5rem;
    min-height: 3rem;
    min-width: 25rem;
    font-size: 1.5rem;
}

._showMore:hover {
    /*border-width: 4px;*/
    border-color: var(--primary);
    cursor: default;
}

._block {
    border: 1px solid var(--dark);
    border-radius: var(--border-radius);
}

._filled {
    background-color: #f8f9fa;
}

._block:hover {
    /*color: var(--primary);*/
}

._activeBlock {
    border-width: 2px !important;
}

._selectable-icon:hover {
    color: var(--primary);
}
._active-icon{
    color: var(--primary);
}

._smallCard{
    border-radius: var(--border-radius);
}